import { ComponentProps, Ref } from 'preact';
import { forwardRef } from 'preact/compat';
import { useCallback } from 'preact/hooks';
import { FocusableHandle, useFocusableTextField } from '../hooks/UNSAFE_useFocusableTextField';
import { useFormContext } from '../hooks/UNSAFE_useFormContext';
import { FormFieldContext } from '../hooks/UNSAFE_useFormFieldContext';
import { useTextField } from '../hooks/UNSAFE_useTextField';
import { useHover } from '../hooks/UNSAFE_useHover';
import { Label } from '../UNSAFE_Label';
import {
  ReadonlyTextField,
  ReadonlyTextFieldInput,
  TextField,
  TextFieldInput
} from '../UNSAFE_TextField';
import { InlineUserAssistance } from '../UNSAFE_UserAssistance';
import { RevealToggleIcon } from './RevealToggleIcon';
import { useClearIcon } from '../hooks/UNSAFE_useClearIcon';
import { beforeVNode } from '../utils/UNSAFE_componentUtils';
import { ClearIcon } from '../UNSAFE_InputText/ClearIcon';
import { Size } from '../utils/UNSAFE_size';
import { useToggle } from '../hooks/UNSAFE_useToggle';

type PickedPropsFromTextField = Pick<ComponentProps<typeof TextField>, 'id'>;

type PickedPropsFromTextFieldInput = Pick<
  ComponentProps<typeof TextFieldInput>,
  | 'autoComplete'
  | 'autoFocus'
  | 'placeholder'
  | 'isRequired'
  | 'textAlign'
  | 'value'
  | 'onInput'
  | 'onCommit'
>;

type PickedPropsFromInlineUserAssistance = Pick<
  ComponentProps<typeof InlineUserAssistance>,
  | 'assistiveText'
  | 'helpSourceLink'
  | 'helpSourceText'
  | 'isRequiredShown'
  | 'messages'
  | 'userAssistanceDensity'
>;

type Props = PickedPropsFromTextField &
  PickedPropsFromTextFieldInput &
  PickedPropsFromInlineUserAssistance & {
    hasClearIcon?: 'always' | 'conditionally' | 'never';
    hasRevealToggle?: 'always' | 'never';
    isDisabled?: boolean;
    isReadonly?: boolean;
    label?: string;
    labelEdge?: 'inside' | 'start' | 'top' | 'none';
    labelStartWidth?: Size;
  };

// One way InputPassword differs from InputText is InputPassword's readonly
// is implemented with an <input> and not a <div>.
export const InputPassword = forwardRef(
  (
    {
      assistiveText,
      autoComplete = 'off',
      autoFocus = false,
      hasClearIcon,
      hasRevealToggle = 'always',
      helpSourceLink,
      helpSourceText,
      id,
      isDisabled: propIsDisabled,
      isReadonly: propIsReadonly,
      isRequired = false,
      isRequiredShown,
      label,
      labelEdge: propLabelEdge,
      messages,
      placeholder,
      textAlign: propTextAlign,
      userAssistanceDensity: propUserAssistanceDensity,
      value,
      onInput,
      onCommit
    }: Props,
    ref?: Ref<FocusableHandle>
  ) => {
    const {
      isDisabled: isFormDisabled,
      isReadonly: isFormReadonly,
      labelEdge: formLabelEdge,
      textAlign: formTextAlign,
      userAssistanceDensity: formUserAssistanceDensity
    } = useFormContext();
    // default to FormContext values if component properties are not specified
    const isDisabled = propIsDisabled ?? isFormDisabled;
    const isReadonly = propIsReadonly ?? isFormReadonly;
    const labelEdge = propLabelEdge ?? formLabelEdge;
    const textAlign = propTextAlign ?? formTextAlign;
    const userAssistanceDensity = propUserAssistanceDensity ?? formUserAssistanceDensity;
    const {
      bool: isRevealed,
      setFalse: setRevealedFalse,
      setTrue: setRevealedTrue
    } = useToggle(false);
    const { enabledElementRef, focusProps, isFocused, readonlyElementRef } = useFocusableTextField<
      HTMLInputElement,
      HTMLInputElement
    >({ isDisabled, isReadonly, ref, onBlurWithin: setRevealedFalse });
    const { hoverProps, isHover } = useHover({ isDisabled: isReadonly || isDisabled || false });
    const { formFieldContext, inputProps, labelProps, textFieldProps, userAssistanceProps } =
      useTextField({
        id,
        isDisabled,
        isFocused,
        isReadonly,
        labelEdge,
        messages,
        value
      });

    // Callback on the RevealToggleButton.
    // When the user clicks on the reveal buttton,
    // toggle revealing/masking password
    const onRevealIconPress = useCallback(() => {
      isRevealed ? setRevealedFalse() : setRevealedTrue();
    }, [isRevealed]);

    // At this point we have returned the readonly component if isReadonly, so now
    // we know we are not readonly. We don't show the revealToggle icon in readonly or disabled.
    const revealToggleIcon =
      !isDisabled && hasRevealToggle === 'always' ? (
        <RevealToggleIcon onPress={onRevealIconPress} isRevealed={isRevealed} />
      ) : null;

    const labelComp = labelEdge !== 'none' ? <Label {...labelProps}>{label}</Label> : undefined;

    const fieldLabelProps = {
      label: labelEdge !== 'none' ? labelComp : undefined,
      labelEdge: labelEdge !== 'none' ? labelEdge : undefined
    };

    const ariaLabel = labelEdge === 'none' ? label : undefined;

    const inlineUserAssistance =
      isDisabled || isReadonly ? (
        // save space for user assistance if density is 'efficient', even though we don't
        // render user assistance for disabled or readonly fields
        userAssistanceDensity !== 'efficient' ? undefined : (
          <InlineUserAssistance
            userAssistanceDensity={userAssistanceDensity}
            {...userAssistanceProps}
          />
        )
      ) : (
        <InlineUserAssistance
          assistiveText={assistiveText}
          helpSourceLink={helpSourceLink}
          helpSourceText={helpSourceText}
          messages={messages}
          isRequiredShown={isRequiredShown}
          userAssistanceDensity={userAssistanceDensity}
          {...userAssistanceProps}
        />
      );

    if (isReadonly) {
      return (
        <FormFieldContext.Provider value={formFieldContext}>
          <ReadonlyTextField
            role="presentation"
            inlineUserAssistance={inlineUserAssistance}
            {...fieldLabelProps}>
            <ReadonlyTextFieldInput
              ariaLabel={ariaLabel}
              ariaLabelledby={labelProps.id}
              as="input"
              autoFocus={autoFocus}
              elementRef={readonlyElementRef}
              id={id}
              textAlign={textAlign}
              type="password"
              value={value}
              hasInsideLabel={label !== undefined && labelEdge === 'inside'}
            />
          </ReadonlyTextField>
        </FormFieldContext.Provider>
      );
    }

    const mainContent = (
      <TextFieldInput
        ariaLabel={ariaLabel}
        autoComplete={autoComplete}
        autoFocus={autoFocus}
        hasInsideLabel={labelComp !== undefined && labelEdge === 'inside'}
        inputRef={enabledElementRef}
        isRequired={isRequired}
        onInput={onInput}
        onCommit={onCommit}
        placeholder={placeholder}
        textAlign={textAlign}
        value={value}
        type={isRevealed ? 'text' : 'password'}
        {...inputProps}
      />
    );

    const onClickClearIcon = useCallback(() => {
      // Clicking the clear icon should put the focus on the input field
      enabledElementRef.current?.focus();
      // Send an event to clear the field's value
      onInput?.({ previousValue: value, value: '' });
    }, [onInput, value]);

    const maybeClearIcon = useClearIcon({
      clearIcon: <ClearIcon onClick={onClickClearIcon} />,
      display: hasClearIcon,
      hasValue: formFieldContext.hasValue,
      isFocused,
      isEnabled: !isReadonly && !isDisabled,
      isHover
    });
    const endContentCombined = beforeVNode(revealToggleIcon, maybeClearIcon);

    return (
      <FormFieldContext.Provider value={formFieldContext}>
        <TextField
          endContent={endContentCombined}
          inlineUserAssistance={inlineUserAssistance}
          mainContent={mainContent}
          onBlur={focusProps.onfocusout}
          onFocus={focusProps.onfocusin}
          {...textFieldProps}
          {...fieldLabelProps}
          {...hoverProps}></TextField>
      </FormFieldContext.Provider>
    );
  }
);
